<template>
  <div class="app-container">
    <h1 class="settings-title"> </h1>
    <pre>
    <!-- {{ appSettings }} -->
    </pre>

    <label class="settings-label">
      <div class="settings-label__text">
          
        <span class="settings-label__req">*</span>
      </div>
      <input
        type="text"
        class="settings-input"
        placeholder="    "
        @input="appTitleEdit"
      />
    </label>

    <label class="settings-label">
      <div class="settings-label__text">
         
        <span class="settings-label__req">*</span>
      </div>
      <textarea
        class="settings-textarea"
        placeholder="    "
        @input="appTitleDescr"
      ></textarea>
    </label>

    <div class="settings-label__text"> </div>
    <app-image-loader
      :addedImage="appSettings.appLogo"
      @imageAdded="addImageInAppSettings"
    />

    <div class="settings-label__text"> </div>
    <app-color-selection
      :colors="appSettings.colors"
      :defaultColor="appSettings.appColor"
      @colorSelect="colorSelect"
    />

    <router-link to="/constructor" class="btn red-btn"> </router-link>
  </div>
</template>

<script>
import AppImageLoader from "./pollSegments/ImageLoader.vue";
import AppColorSelection from "./pollSegments/ColorSelection.vue";
import { mapState, mapMutations } from "vuex";

export default {
  components: {
    AppImageLoader,
    AppColorSelection,
  },
  computed: {
    ...mapState({
      appSettings: (state) => state.appSettings,
    }),
  },
  data() {
    return {};
  },
  methods: {
    ...mapMutations(["editAppSettings"]),
    colorSelect(selectedColor) {
      this.editAppSettings({
        field: "appColor",
        payload: selectedColor,
      });
    },
    addImageInAppSettings(imageData) {
      this.editAppSettings({
        field: "appLogo",
        payload: imageData,
      });
    },
    appTitleEdit(e) {
      const value = e.target.value.trim();
      this.editAppSettings({
        field: "appTitle",
        payload: value,
      });
    },
    appTitleDescr(e) {
      const value = e.target.value.trim();
      this.editAppSettings({
        field: "appDescription",
        payload: value,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.poll-dropzone__container {
  margin-bottom: 30px;
}
.settings-label {
  display: block;
  margin-bottom: 30px;
  width: 100%;
}
.settings-label__text {
  color: #262b31;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%; /* 25.2px */
  margin-bottom: 10px;
  .settings-label__req {
    color: #eb3333;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 27px */
  }
}
.settings-input,
.settings-textarea {
  outline: none !important;
  border-radius: 4px;
  border: 1px solid rgba(0, 66, 105, 0.28);
  background: #fff;
  width: 100%;
  padding: 10px 12px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%; /* 27px */
}
.settings-textarea {
  resize: vertical;
  min-height: 150px;
}
</style>
